import React, { useState, useEffect } from 'react';
import { jsPDF } from "jspdf";
import "jspdf-autotable";
import * as XLSX from 'xlsx';

const colors = {
  background: "#1e1e1e",
  cardBg: "#2d2d2d",
  primary: "#4a4a4a",
  text: "#ffffff",
  accent: "#3a86ff",
  highlight: "#4caf50",
};

const categories = [
  { name: 'Kameras', icon: 'videocam' },
  { name: 'Objektive', icon: 'camera' },
  { name: 'Kameragrip', icon: 'pan_tool' },
  { name: 'Licht', icon: 'wb_sunny' },
  { name: 'Ton', icon: 'mic' },
  { name: 'Monitoring', icon: 'desktop_windows' },
  { name: 'Stromversorgung', icon: 'power' },
  { name: 'Verbrauch', icon: 'shopping_cart' }
];

const AutocompleteInput = ({ items, onSelect, onAddNew }) => {
  const [inputValue, setInputValue] = useState('');
  const [filteredItems, setFilteredItems] = useState([]);
  const [isOpen, setIsOpen] = useState(false);
  const [showAddButton, setShowAddButton] = useState(false);
const [showAccessoryInput, setShowAccessoryInput] = useState({});

  useEffect(() => {
    if (inputValue.length > 0) {
      const filtered = items.filter(item =>
        item.name.toLowerCase().includes(inputValue.toLowerCase()) ||
        item.brand.toLowerCase().includes(inputValue.toLowerCase())
      );
      setFilteredItems(filtered);
      setIsOpen(true);
      setShowAddButton(filtered.length === 0);
    } else {
      setFilteredItems([]);
      setIsOpen(false);
      setShowAddButton(false);
    }
  }, [inputValue, items]);

  const handleSelect = (item) => {
    setInputValue('');
    onSelect(item);
    setIsOpen(false);
  };

  const handleAddNew = () => {
    onAddNew(inputValue);
    setInputValue('');
    setShowAddButton(false);
  };

  return (
    <div className="relative">
      <div className="flex">
        <input
          type="text"
          value={inputValue}
          onChange={(e) => setInputValue(e.target.value)}
          placeholder="Suche nach Ausrstung oder Marke..."
          className="w-full p-3 rounded-lg mb-2 text-lg"
          style={{ backgroundColor: colors.cardBg, color: colors.text, border: `1px solid ${colors.primary}` }}
        />
        {showAddButton && (
          <button
            onClick={handleAddNew}
            className="ml-2 px-4 py-2 rounded text-white text-lg font-semibold"
            style={{ backgroundColor: colors.accent }}
          >
            Hinzufgen
          </button>
        )}
      </div>
      {isOpen && (
        <ul className="absolute z-10 w-full bg-white border border-gray-300 rounded-lg mt-1 max-h-60 overflow-auto">
          {filteredItems.map((item, index) => (
            <li
              key={index}
              onClick={() => handleSelect(item)}
              className="p-2 hover:bg-gray-100 cursor-pointer"
              style={{ color: colors.background }}
            >
              {item.name}
            </li>
          ))}
        </ul>
      )}
    </div>
  );
};

const App = () => {
  const [activeWindow, setActiveWindow] = useState('select');
  const [equipment, setEquipment] = useState({});
  const [selectedEquipment, setSelectedEquipment] = useState([]);
  const [allEquipment, setAllEquipment] = useState([]);
  const [projectName, setProjectName] = useState('');
  const [cameramanName, setCameramanName] = useState('');
  const [projectDate, setProjectDate] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('');
  const [selectedBrand, setSelectedBrand] = useState('');
  const [selectedSeries, setSelectedSeries] = useState('');
  const [exportComment, setExportComment] = useState('');
  const [showAccessories, setShowAccessories] = useState({});

  useEffect(() => {
    const loadAllEquipment = async () => {
      const equipmentList = [];
      const loadedEquipment = {};
      for (const category of categories) {
        try {
          const response = await fetch(`/${category.name.toLowerCase()}.json`);
          const categoryData = await response.json();
          loadedEquipment[category.name] = categoryData;
          Object.entries(categoryData).forEach(([brand, series]) => {
            Object.entries(series).forEach(([seriesName, items]) => {
              equipmentList.push(...items.map(item => ({ 
                name: `${brand} ${item}`, 
                category: category.name, 
                brand, 
                series: seriesName,
                searchTerms: `${brand} ${item}`.toLowerCase() 
              })));
            });
          });
        } catch (error) {
          console.error(`Error loading ${category.name} data:`, error);
          loadedEquipment[category.name] = {};
        }
      }
      setAllEquipment(equipmentList);
      setEquipment(loadedEquipment);
    };

    loadAllEquipment();
    setProjectDate(new Date().toISOString().split('T')[0]);
  }, []);

  const addNewItem = (itemName) => {
    const category = prompt("Zu welcher Kategorie gehrt dieses Ausrstungsteil?", categories[0].name);
    if (category) {
      const newItem = {
        name: itemName,
        category: category,
        brand: 'Unbekannt',
        series: 'Standard',
        searchTerms: itemName.toLowerCase()
      };
      setAllEquipment(prev => [...prev, newItem]);
      addEquipment(newItem);
    }
  };

  const addEquipment = (item) => {
    setSelectedEquipment(prev => {
      const existingItem = prev.find(e => e.name === item.name);
      if (existingItem) {
        return prev.map(e => e.name === item.name ? { ...e, quantity: e.quantity + 1 } : e);
      } else {
        return [...prev, { ...item, quantity: 1, accessories: [] }];
      }
    });
  };

  const removeEquipment = (name) => {
    setSelectedEquipment(prev => prev.filter(item => item.name !== name));
  };

  const updateQuantity = (name, change) => {
    setSelectedEquipment(prev => prev.map(item => 
      item.name === name ? { ...item, quantity: Math.max(1, item.quantity + change) } : item
    ));
  };

  const toggleAccessories = (itemName) => {
    setShowAccessories(prev => ({...prev, [itemName]: !prev[itemName]}));
  };

  const addAccessory = (itemName, accessory) => {
    setSelectedEquipment(prev => prev.map(item =>
      item.name === itemName
        ? { ...item, accessories: [...(item.accessories || []), accessory] }
        : item
    ));
  };

  const removeAccessory = (itemName, accessoryIndex) => {
    setSelectedEquipment(prev => prev.map(item =>
      item.name === itemName
        ? { ...item, accessories: item.accessories.filter((_, index) => index !== accessoryIndex) }
        : item
    ));
  };

  const saveProject = () => {
    const project = {
      projectName,
      cameramanName,
      projectDate,
      selectedEquipment,
      exportComment
    };
    const blob = new Blob([JSON.stringify(project)], { type: 'application/json' });
    const href = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = href;
    link.download = `${projectName || 'project'}.json`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  const loadProject = (file) => {
    const reader = new FileReader();
    reader.onload = (e) => {
      const project = JSON.parse(e.target.result);
      setProjectName(project.projectName);
      setCameramanName(project.cameramanName);
      setProjectDate(project.projectDate);
      setSelectedEquipment(project.selectedEquipment);
      setExportComment(project.exportComment);
    };
    reader.readAsText(file);
  };

  const loadStandardList = (listName) => {
    fetch(`/standardLists/${listName}.json`)
      .then(response => response.json())
      .then(data => {
        setSelectedEquipment(data.selectedEquipment);
      })
      .catch(error => console.error('Error loading standard list:', error));
  };

  const exportToPDF = () => {
    const doc = new jsPDF();
    doc.setFontSize(20);
    doc.setTextColor(40, 40, 40);
    doc.text("Equipment Liste", 14, 22);
    
    doc.setFontSize(12);
    doc.setTextColor(60, 60, 60);
    doc.text(`Projekt: ${projectName}`, 14, 32);
    doc.text(`Kameramann: ${cameramanName}`, 14, 38);
    doc.text(`Datum: ${projectDate}`, 14, 44);
    
    let yPos = 54;
    
    categories.forEach(category => {
      const items = selectedEquipment.filter(item => item.category === category.name);
      if (items.length > 0) {
        doc.setFontSize(14);
        doc.setTextColor(0, 102, 204);
        doc.text(category.name, 14, yPos);
        yPos += 8;
        
        doc.setFontSize(10);
        doc.setTextColor(60, 60, 60);
        items.forEach(item => {
          doc.text(`${item.name} (${item.quantity}x)`, 20, yPos);
          yPos += 5;
          if (item.accessories && item.accessories.length > 0) {
            item.accessories.forEach(acc => {
              doc.text(`- ${acc}`, 25, yPos);
              yPos += 5;
            });
          }
          yPos += 2;
        });
        yPos += 5;
      }
    });

    if (exportComment) {
      yPos += 5;
      doc.setFontSize(14);
      doc.setTextColor(0, 102, 204);
      doc.text("Kommentar:", 14, yPos);
      yPos += 8;
      doc.setFontSize(10);
      doc.setTextColor(60, 60, 60);
      const splitComment = doc.splitTextToSize(exportComment, 180);
      doc.text(splitComment, 14, yPos);
    }
    
    doc.save("equipment_liste.pdf");
  };

  const exportToExcel = () => {
    const wb = XLSX.utils.book_new();
    
    const equipmentData = categories.flatMap(category => {
      const items = selectedEquipment.filter(item => item.category === category.name);
      return [
        [category.name],
        ['Ausrstung', 'Menge', 'Zubehr'],
        ...items.map(item => [
          item.name,
          item.quantity,
          item.accessories ? item.accessories.join(', ') : ''
        ]),
        []  // Empty row for spacing
      ];
    });

    const ws = XLSX.utils.aoa_to_sheet([
      ['Equipment Liste'],
      [`Projekt: ${projectName}`],
      [`Kameramann: ${cameramanName}`],
      [`Datum: ${projectDate}`],
      [],  // Empty row for spacing
      ...equipmentData,
      [],  // Empty row for spacing
      ['Kommentar'],
      [exportComment]
    ]);

    // Set column widths
    ws['!cols'] = [{ wch: 30 }, { wch: 10 }, { wch: 50 }];

    XLSX.utils.book_append_sheet(wb, ws, "Equipment Liste");
    XLSX.writeFile(wb, "equipment_liste.xlsx");
  };

  const renderProjectMenu = () => (
    <div className="p-6">
      <h1 className="text-3xl font-bold mb-6" style={{ color: colors.text }}>Projekt Men</h1>
      <div className="mb-4">
        <input
          type="text"
          value={projectName}
          onChange={(e) => setProjectName(e.target.value)}
          placeholder="Projektname"
          className="w-full p-2 mb-2 rounded"
          style={{ backgroundColor: colors.cardBg, color: colors.text }}
        />
        <input
          type="text"
          value={cameramanName}
          onChange={(e) => setCameramanName(e.target.value)}
          placeholder="Name des Kameramanns"
          className="w-full p-2 mb-2 rounded"
          style={{ backgroundColor: colors.cardBg, color: colors.text }}
        />
        <input
          type="date"
          value={projectDate}
          onChange={(e) => setProjectDate(e.target.value)}
          className="w-full p-2 mb-2 rounded"
          style={{ backgroundColor: colors.cardBg, color: colors.text }}
        />
      </div>
      <div className="mb-4">
        <h2 className="text-xl font-bold mb-2" style={{ color: colors.text }}>Projekt laden</h2>
        <input
          type="file"
          accept=".json"
          onChange={(e) => loadProject(e.target.files[0])}
          className="mb-2"
        />
      </div>
      <div>
        <h2 className="text-xl font-bold mb-2" style={{ color: colors.text }}>Standardlisten</h2>
        {["DUDW auf FX3", "DUDW auf FX9", "3 KAMERA IV", "2 KAMERA IV", "JKP7 Standard Dreh", "Szenischer Standard Dreh"].map(list => (
          <button
            key={list}
            onClick={() => loadStandardList(list)}
            className="w-full p-2 mb-2 rounded"
            style={{ backgroundColor: colors.accent, color: colors.text }}
          >
            {list}
          </button>
        ))}
      </div>
    </div>
  );

  const renderSelectWindow = () => (
    <div className="p-6">
<h1 className="text-3xl font-bold mb-6" style={{ color: colors.text }}>Equipment Auswahl</h1>
      <AutocompleteInput items={allEquipment} onSelect={addEquipment} onAddNew={addNewItem} />
      <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 gap-4 mb-6">
        {categories.map(category => (
          <button
            key={category.name}
            onClick={() => {
              setSelectedCategory(category.name);
              setSelectedBrand('');
              setSelectedSeries('');
            }}
            className={`p-4 rounded-lg flex flex-col items-center justify-center ${selectedCategory === category.name ? 'ring-2 ring-offset-2' : ''}`}
            style={{ 
              backgroundColor: colors.cardBg,
              borderColor: selectedCategory === category.name ? colors.highlight : 'transparent',
              borderWidth: '2px',
            }}
          >
            <span className="material-icons mb-2">{category.icon}</span>
            <span>{category.name}</span>
          </button>
        ))}
      </div>
      {selectedCategory && (
        <div className="mb-4">
          <select
            className="w-full p-3 rounded-lg mb-2 text-lg"
            style={{ backgroundColor: colors.cardBg, color: colors.text, border: `1px solid ${colors.primary}` }}
            value={selectedBrand}
            onChange={(e) => {
              setSelectedBrand(e.target.value);
              setSelectedSeries('');
            }}
          >
            <option value="">Whle einen Hersteller</option>
            {Object.keys(equipment[selectedCategory] || {}).map(brand => (
              <option key={brand} value={brand}>{brand}</option>
            ))}
          </select>
          {selectedBrand && equipment[selectedCategory] && equipment[selectedCategory][selectedBrand] && (
            <select
              className="w-full p-3 rounded-lg mb-2 text-lg"
              style={{ backgroundColor: colors.cardBg, color: colors.text, border: `1px solid ${colors.primary}` }}
              value={selectedSeries}
              onChange={(e) => setSelectedSeries(e.target.value)}
            >
              <option value="">Whle eine Serie</option>
              {Object.keys(equipment[selectedCategory][selectedBrand] || {}).map(series => (
                <option key={series} value={series}>{series}</option>
              ))}
            </select>
          )}
        </div>
      )}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {selectedCategory && selectedBrand && selectedSeries &&
          (equipment[selectedCategory][selectedBrand][selectedSeries] || []).map(item => (
            <div key={item} className="p-4 rounded-lg" style={{ backgroundColor: colors.cardBg }}>
              <h3 className="font-bold text-xl mb-3" style={{ color: colors.text }}>{`${selectedBrand} ${item}`}</h3>
              <button 
                onClick={() => addEquipment({name: `${selectedBrand} ${item}`, category: selectedCategory})}
                className="w-full px-4 py-2 rounded text-white text-lg font-semibold"
                style={{ backgroundColor: colors.accent }}
              >
                Hinzufgen
              </button>
            </div>
          ))
        }
      </div>
    </div>
  );

  const renderEquipmentList = () => (
    <div className="h-full p-4 overflow-auto" style={{ backgroundColor: colors.cardBg }}>
      <h2 className="text-2xl font-bold mb-4" style={{ color: colors.text }}>Ausgewhlte Ausrstung</h2>
      {categories.map(category => {
        const items = selectedEquipment.filter(item => item.category === category.name);
        if (items.length === 0) return null;
        return (
          <div key={category.name} className="mb-4">
            <h3 className="text-xl font-bold mb-2" style={{ color: colors.accent }}>
              <span className="material-icons mr-2 align-bottom">{category.icon}</span>
              {category.name}
            </h3>
            {items.map((item) => (
              <div
                key={item.name}
                className="flex justify-between items-center mb-2 p-2 rounded-lg"
                style={{ backgroundColor: colors.background }}
              >
                <span className="font-medium text-sm" style={{ color: colors.text }}>{item.name}</span>
                <div className="flex items-center">
                  <button onClick={() => updateQuantity(item.name, -1)} className="px-2 py-1 rounded-l text-white font-bold text-sm" style={{ backgroundColor: colors.primary }}>-</button>
                  <span className="mx-2 font-bold text-sm" style={{ color: colors.text }}>{item.quantity}</span>
                  <button onClick={() => updateQuantity(item.name, 1)} className="px-2 py-1 rounded-r text-white font-bold text-sm" style={{ backgroundColor: colors.primary }}>+</button>
                  <button onClick={() => removeEquipment(item.name)} className="ml-2 px-2 py-1 rounded text-white font-bold text-sm" style={{ backgroundColor: colors.accent }}>X</button>
                  <button onClick={() => toggleAccessories(item.name)} className="ml-2 px-2 py-1 rounded text-white font-bold text-sm" style={{ backgroundColor: colors.highlight }}>Zubehr</button>
                </div>
              </div>
            ))}
            {items.map((item) => (
              showAccessories[item.name] && (
                <div key={`${item.name}-accessories`} className="mt-2 ml-4">
                  <h4 className="font-bold mb-1" style={{ color: colors.accent }}>Zubehr:</h4>
                  <ul className="list-disc pl-4">
                    {(item.accessories || []).map((accessory, accIndex) => (
                      <li key={accIndex} className="flex justify-between items-center">
                        <span style={{ color: colors.text }}>{accessory}</span>
                        <button 
                          onClick={() => removeAccessory(item.name, accIndex)}
                          className="ml-2 px-1 rounded text-white text-xs"
                          style={{ backgroundColor: colors.accent }}
                        >
                          X
                        </button>
                      </li>
                    ))}
                  </ul>
                  <div className="mt-2 flex">
                    <input
                      type="text"
                      placeholder="Neues Zubehr"
                      className="flex-grow p-1 rounded-l"
                      style={{ backgroundColor: colors.cardBg, color: colors.text }}
                      id={`accessory-input-${item.name}`}
                    />
                    <button
                      onClick={() => {
                        const input = document.getElementById(`accessory-input-${item.name}`);
                        if (input.value) {
                          addAccessory(item.name, input.value);
                          input.value = '';
                        }
                      }}
                      className="px-2 py-1 rounded-r text-white font-bold text-sm"
                      style={{ backgroundColor: colors.highlight }}
                    >
                      +
                    </button>
                  </div>
                </div>
              )
            ))}
          </div>
        );
      })}
    </div>
  );

  const renderExportWindow = () => (
    <div className="p-6">
      <h1 className="text-3xl font-bold mb-6" style={{ color: colors.text }}>Export</h1>
      <div className="mb-6">
        <label className="block mb-2" style={{ color: colors.text }}>Kommentar hinzufgen:</label>
        <textarea
          value={exportComment}
          onChange={(e) => setExportComment(e.target.value)}
          className="w-full p-2 rounded"
          rows="4"
          style={{ backgroundColor: colors.background, color: colors.text, border: `2px solid ${colors.accent}` }}
        ></textarea>
      </div>
      <button onClick={exportToPDF} className="w-full px-4 py-3 rounded mb-3 text-white text-lg font-semibold" style={{ backgroundColor: colors.accent }}>Als PDF exportieren</button>
      <button onClick={exportToExcel} className="w-full px-4 py-3 rounded mb-3 text-white text-lg font-semibold" style={{ backgroundColor: colors.primary }}>Als Excel exportieren</button>
      <button onClick={saveProject} className="w-full px-4 py-3 rounded mb-3 text-white text-lg font-semibold" style={{ backgroundColor: colors.highlight }}>Projekt speichern</button>
    </div>
  );

  return (
    <div className="flex h-screen font-sans" style={{ backgroundColor: colors.background, color: colors.text }}>
      {/* Navigation Sidebar */}
      <div className="w-24 p-4 flex flex-col items-center" style={{ backgroundColor: colors.cardBg }}>
        <button onClick={() => setActiveWindow('project')} className={`w-16 h-16 mb-4 rounded-lg flex items-center justify-center ${activeWindow === 'project' ? 'bg-blue-500' : 'bg-gray-700'}`}>
          <span className="material-icons">folder</span>
        </button>
        <button onClick={() => setActiveWindow('select')} className={`w-16 h-16 mb-4 rounded-lg flex items-center justify-center ${activeWindow === 'select' ? 'bg-blue-500' : 'bg-gray-700'}`}>
          <span className="material-icons">search</span>
        </button>
        <button onClick={() => setActiveWindow('export')} className={`w-16 h-16 mb-4 rounded-lg flex items-center justify-center ${activeWindow === 'export' ? 'bg-blue-500' : 'bg-gray-700'}`}>
          <span className="material-icons">file_download</span>
        </button>
      </div>

      {/* Main content area */}
      <div className="flex-1 flex overflow-hidden">
        <div className="flex-1 overflow-auto">
          {activeWindow === 'project' && renderProjectMenu()}
          {activeWindow === 'select' && renderSelectWindow()}
          {activeWindow === 'export' && renderExportWindow()}
        </div>
        
        {/* Permanent Equipment List */}
        <div className="w-1/4 min-w-[300px] border-l border-gray-600">
          {renderEquipmentList()}
        </div>
      </div>

      {/* Project Info Bar */}
      <div className="absolute bottom-0 left-0 right-0 p-4 bg-gray-800">
        <div className="flex justify-between items-center">
          <div>Projekt: {projectName}</div>
          <div>Kameramann: {cameramanName}</div>
          <div>Datum: {projectDate}</div>
        </div>
      </div>
    </div>
  );
};

export default App;